.landing {
  height: 1700px;
  background-color: #EEEEE6; }
  .landing-background {
    background: url(../images/background.jpg) center center;
    background-size: cover;
    height: 500px; }
    @media (min-width: 768px) {
      .landing-background {
        background: url(../images/background.jpg) center center;
        background-size: cover;
        height: 700px; } }
    @media screen and (min-width: 900px) {
      .landing-background {
        height: 900px; } }
    @media (min-width: 1200px) {
      .landing-background {
        height: 1200px; } }
    @media screen and (min-height: 900px) {
      .landing-background {
        height: 1400px; } }
    @media screen and (min-width: 1440px) {
      .landing-background {
        height: 1408px; } }
  .landing-info {
    position: absolute;
    text-align: center;
    font-weight: 700;
    font-size: 1.2em;
    width: 80%;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 50px; }
    .landing-info-date {
      color: #FFA949; }
    @media screen and (min-width: 1440px) {
      .landing-info {
        -webkit-transform: translate(100px, -571px);
            -ms-transform: translate(100px, -571px);
                transform: translate(100px, -571px);
        font-size: 1.9em;
        color: white;
        width: 400px; } }
  .landing-title {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    position: absolute; }
    .landing-title img {
      width: 240px; }
    @media screen and (min-width: 1440px) {
      .landing-title {
        -webkit-transform: translate(-550px, -530px);
            -ms-transform: translate(-550px, -530px);
                transform: translate(-550px, -530px); }
        .landing-title img {
          width: 400px; } }



